.machine {
    background-color: rgb(245, 245, 245);
    border: 3px solid black;
    border-radius: 15px;
    margin-bottom: 20px;
    &__panel {
        display: grid;
        grid-template-columns: 40% 60%;        
        margin-bottom: 50px;
        border-bottom: 3px solid black;
        font-size: 16px !important;
        &_title {
            background-color: rgb(105, 59, 59);
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            color: white;
            border-radius: 12px 0px 0px 0px;
            font-size: 24px;
            font-family: 'Press Start 2P', cursive;

        }
    }

    &__body {
        padding: 0px 30px 30px 30px;
        display: grid;
        grid-template-columns: 65% 35%;
    }
}

.panel {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-radius: 0px 15px 0px 0px;
    background-color: bisque;
    padding: 30px 35px;

    &__button {
        padding: 10px 15px;
        margin: 0px 10px;
        // border: 2px inset black;
        border-radius: 15px;
        background-color: rgb(62, 57, 57);
        color: white;
        font-size: larger;
        border-bottom: 4px solid #b2b1b1;
        text-shadow: 0px 1px 0px #fafafa;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &:hover {
            cursor: pointer;
            background-color: rgb(93, 92, 92);
            transition: 0.3s;
        }
    }
    &__el {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
}

.actionLog {
    display: flex;
    flex-direction: column;

    border: 1px solid black;
    border-radius: 10px;
    margin: 10px;

    background-image: url(https://img.freepik.com/premium-photo/white-texture-watercolor-paper_88281-381.jpg?w=2000);

    -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);

    &__header {
        border-radius: 10px 10px 0px 0px;
        text-align: center;
        padding: 15px 0px;
        background-color: black;
        color: white;
    }

    &__body {
        max-height: 550px;
        padding: 10px 5px ;
        color: rgb(0, 0, 0);
        overflow-y: auto;
        overflow-x: auto;
    }
}

.tape {
    display: flex;
    flex-direction: row;
    // margin-bottom: 20px;
    margin: 10px auto;
    justify-content: space-around;
    align-items: center;
    // min-width: 100%;

    &__left, &__right {
        padding: 20px;
        background-color: black;
        color: white;
    }
    &__left:hover, &__right:hover {
        cursor: pointer;
        transition: 0.2s;
        padding: 20px;
        background-color: rgb(87, 87, 87);
        color: white;
    }
    
    &__cells {
        display: flex;
        flex-direction: row;
        background-color: white;
    }

}

.cell {
    
    box-sizing: content-box;

    min-width: 45px;
    min-height: 50px;
    padding: 5px;
    border: 1px solid black;

    // display: flex;
    // flex-direction: row;
    text-align: center;
    // padding: 5px auto;

    font-size: 40px;
    font-weight: 700;
    // align-items: center;
}

.currCell {
    box-sizing: content-box;

    min-width: 45px;
    min-height: 30px;
    padding: 3px;
    border: 3px dashed black;
    background-color: #f5e9e9;

    text-align: center;

    font-size: 40px;
    font-weight: 700;
}


.currentState {
    margin: 0px auto;
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-right: 10px;

    font-size: 30px;
    font-weight: 800;
}


.inputEditCell {
    width: 30px;
    height: 40px;
    font-size: 35px;
}

